import { feedbackList } from '@/assets/list/feedbackList'
if (process.browser) { // Here we introduce... According to the environment wow.js
  var { WOW } = require('wowjs')
}
export default {
  head: {
    title: '職涯諮詢 / 接案諮詢 / 接案陪跑 - 西打藍 接案陪跑 / 商業行銷顧問',
    meta: [
      { name: 'keywords', content: '西打藍, 西打藍官方網站, 自由工作者, 自由接案, 接案諮詢, 接案陪跑, 職涯諮詢, 接案, 諮詢' },
    ]
  },
  layout: 'default',
  components: {
    preBox: require('~/components/common/pre-box.vue').default,
    boxContent: require('~/components/common/box-content.vue').default,
    headText: require('~/components/common/head-text.vue').default,
    titleText: require('~/components/common/title-text.vue').default,
    contactBtn: require('~/components/common/contact-btn.vue').default,
  },
  props: {

  },
  data() {
    return {
      form: {
        name: '',
        mail: '',
        way: '',
        time: '',
        account: '',
        question: '',
        other: ''
      },
      isForm: {
        name: true,
        mail: true,
        way: true,
        time: true,
        account: true,
        question: true
      },
      isEmpty: false,
      feedbacks: feedbackList
    }
  },
  mounted() {
    // wow
    this.$nextTick(() => {
      if (process.browser) { // On the page mounted In the life cycle Instantiate according to the environment WOW
        new WOW({
          boxClass: 'wow',
          animateClass: 'animated',
          offset: 0,
          mobile: true,
          live: false,
          callback: null,
          scrollContainer: null
        }).init()
      }
    })
  },
  computed: {

  },
  methods: {
    focusInput(focus) {
      if (focus == 'name') {
        this.isForm.name = true
      } else if (focus == 'mail') {
        this.isForm.mail = true
      } else if (focus == 'way') {
        this.isForm.way = true
      } else if (focus == 'time') {
        this.isForm.time = true
      } else if (focus == 'account') {
        this.isForm.account = true
      } else if (focus == 'question') {
        this.isForm.question = true
      }
    },
    handleSubmit() {
      if (!this.form.name) {
        this.isForm.name = false
      }
      if (!this.form.mail) {
        this.isForm.mail = false
      }
      if (!this.form.way) {
        this.isForm.way = false
      }
      if (!this.form.time) {
        this.isForm.time = false
      }
      if (!this.form.account) {
        this.isForm.account = false
      }
      if (!this.form.question) {
        this.isForm.question = false
      }
      if (!this.form.name || !this.form.mail || !this.form.way || !this.form.time || !this.form.account || !this.form.question) {
        return
      }



      const formUrl = 'https://docs.google.com/forms/d/e/1FAIpQLScmBMPF3Td-gwCXHIjHSs6W1bp5SQc3gKSJ0XIChnRiivJpqw/formResponse'
      const queryString = require('query-string')
      const q = queryString.stringifyUrl({
        url: formUrl,
        query: {
          'entry.1262132982': this.form.name,
          'entry.1910135673': this.form.mail,
          'entry.1674093959': this.form.way,
          'entry.1011952548': this.form.time,
          'entry.795183849': this.form.account,
          'entry.1272676557': this.form.question,
          'entry.602373435': this.form.other
        }
      })
      this.$axios.post(q)
        .then(response => {

        })
        .catch(error => {

        })

      document.getElementById('formbox').scrollIntoView({ behavior: 'smooth' })
      this.isEmpty = true
    }
  },
  watch: {

  }
}
